.grid {
  display: grid;
  grid-template-columns: repeat(14, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  }
  
  .nav { grid-area: 1 / 2 / 2 / 14;
   background-color: #282c34;
  color: white;
  height: 67px;
  padding-top: 10px;
  padding-left: 24px;
  margin-top: 0px;
}

  .address { grid-area: 2 / 2 / 3 / 14;
  padding-left: 24px;}
  .mainPhoto { grid-area: 3 / 2 / 5 / 8; }
  .photo2 { grid-area: 3 / 8 / 4 / 11; }
  .photo3 { grid-area: 3 / 11 / 4 / 14; }
  .photo4 { grid-area: 4 / 8 / 5 / 11; }
  .photo5 { grid-area: 4 / 11 / 5 / 14; }
  .description { grid-area: 5 / 2 / 6 / 11; 
  padding-left: 12px;
margin-top: 42px;}
  .application { grid-area: 5 / 11 / 6 / 14; 
  margin-top: 42px;}
  .footer { grid-area: 6 / 2 / 7 / 14; 
  margin-top: 24px;}

  .myButton {
    background-color: #282C34;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border-radius: 12px;
  }
  
  img {
    object-fit: cover;
    width: 100%;
    max-height: 100%;
  }

  @media screen and (max-width: 600px) {
    .grid {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 0px;
      grid-row-gap: 0px;
      }
      
      .nav { grid-area: 1 / 1 / 2 / 3; }
      .address { grid-area: 2 / 1 / 3 / 3; }
      .mainPhoto { grid-area: 3 / 1 / 4 / 3; }
      .photo2 { grid-area: 4 / 1 / 5 / 3; display: none; }
      .photo3 { grid-area: 5 / 1 / 6 / 3; display: none;}
      .photo4 { grid-area: 6 / 1 / 7 / 3; display: none;}
      .photo5 { grid-area: 7 / 1 / 8 / 3; display: none;}
      .description { grid-area: 5 / 1 / 6 / 3; }
      .application { grid-area: 6 / 1 / 7 / 3; 
      margin-left: 10%;
    margin-right: 10%;
  text-align: justify;}
      .footer { grid-area: 7 / 1 / 8 / 3; }
      p, h1, h2, table, tr, td, a {
        text-align: center;
      }
    
      .myButton {
        margin-left: 10%;
        margin-right: 10%;
        width: 60%;
      }
  }
